<template>
  <div class="select">
    <app-header title="重卡汇"></app-header>
    <div style="width: 100%">
      <router-link to="/home/bind">绑定重卡汇账号</router-link>
      <router-link to="/home/account/set">注册重卡汇账号</router-link>
    </div>
  </div>
</template>

<script>
  import AppHeader from '../../../components/header/header.vue'
  export default {
    components: {AppHeader}
  }
</script>

<style lang="less" scoped>
  .select {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    a {
      display: block;
      color: #333;
      padding: 2rem;
      background: #fff;
      font-size: 1.5rem;
      margin-top: 1rem;
      &:first-child{
        margin-top: 0;
      }
    }
  }
</style>



